
@media screen and (max-width: 600px)

	#myhello
		.scarec
			height: 4.5rem
			margin-right: 0.5rem
	
	body
		overflow-x: hidden
	
	aside
		display: none

	.mobile-menu
		display: block
		cursor: pointer
		font-family: $fontEmoji
		font-size: 2rem
		font-weight: bold
		line-height: 1.75rem
		background-color: #0000ff75
		border: 0.05rem solid #0000ff50
		border-radius: 0.3rem
		margin: 1rem 0
		padding: 0 0.5rem
		
	#main-nav
		flex-flow: column nowrap
		visibility: hidden
		position: absolute
		top: 4.4375rem
		right: 0
		min-width: 11rem
		max-width: 75%
		border-left: 1px solid rgba($mainColor, 0.25)
		border-bottom: 1px solid rgba($mainColor, 0.25)
		border-collaps: collaps
		background-color: #fff

		a
			display: block
			height: auto
			background-color: #fff
			height: 2.5rem
			line-height: 2.4rem
			padding-left: 7.5%
			border: unset
			border-radius: unset
			border-top: 1px solid rgba($mainColor, 0.25)

		#subpage-nav
			display: block
			max-height: 12rem
			overflow-y: auto
			background-color: #ffffff
				
			a
				background-color: #fff
				padding: 0.2rem 0.2rem 0.2rem 15%
				height: auto
				width: auto
				border: none
				border-top: 1px dotted darkred
				line-height: 2rem
				color: mediumblue
					
			.activeUrl
				background: url("../images/asign.svg") 0.5rem center / 0.75rem 0.75rem no-repeat, #f5f5f5
				color: darkred

