
*
	padding: 0
	margin: 0

html
	background-color: $htmlbgc
	font-family: $fonthtml
	font-size: 1rem
	line-height: 1.5rem
	color: $mainColor

h1, h2, h3, h4, h5, h6
	font-family: $fontAlice
	line-height: 3rem

h1
	font-size: 1.75rem

h2
	font-size: 1.6rem

h3
	font-size: 1.45rem

h4
	font-size: 1.3rem

h5
	font-size: 1.15rem

h6
	font-size: 1.05rem

p
	hyphens: auto

a, a:visited
	color: mediumblue

ol, ul
	margin-left: 5%
	border: none

	li ol, li ul
		margin-left: 5%

li::marker
	line-height: 1.25rem

.itemList
	ul, ol
		list-style-type: none
		margin: 0
		padding: 0
		
		li h2
			margin-left: 2rem

abbr
	cursor: pointer

	&:focus::before
		font-family: $fonthtml
		font-size: 1rem
		display: inline-block
		position: absolute
		visibility: visible
		max-width: 7.8em
		border: 1px dotted #000000
		content: attr(title)
		line-height: 1em
		padding: 0.2em
		background-color: #f5f5f5
		color: #000000

.simbol
	font-family: $fontSimbol

.emoji
	font-family: $fontEmoji
	font-size: 1.25rem
	line-height: 0.9rem

code
	font-family: $fontMono
	font-size: 0.95rem
	line-height: 1rem
	padding: 0 0.25rem
	border-radius: 0.25rem
	border: 2px solid #00000015
	background-color: #00000010
	color: #000000

.highlighter-rouge
	margin: 0.375rem 0

pre
	padding: calc(0.375rem - 2px) 0.75rem

pre > code
	padding: unset
	background-color: unset
	border: unset

.rightside
	padding: 1.5rem 10% 0 38%

table
	border-collapse: collapse
	margin: 0.75rem 0 calc(0.75rem - 1px) 0

	td, th
		border: 1px solid black

	td, th
		height: calc(3rem - 1px)
		padding: 0 1rem

th
	font-size: large

body
	display: flex
	flex-flow: row wrap
	justify-content: space-between
	max-width: 50rem
	margin: 0 auto
	background: $bodybg
	box-shadow: 0 0 0.2rem lightgray
	filter: drop-shadow(0 0 0.2rem lightgray)
	color: $mainColor

header
	width: 100%
	height: 4.5rem
	display: flex
	flex-flow: row nowrap
	justify-content: space-between
	background: $headerbg
	padding: 0 1.5rem

section
	margin-bottom: 1.5rem

.stitle
	align-self: center

	a
		display: block
		height: 2rem

.cool
	height: 2rem

.mobile-menu
	display: none
		
#main-nav
	visibility: visible
	align-self: flex-end
	display: flex
	flex-flow: row nowrap
	justify-content: flex-end
	margin-bottom: 0.25rem
		
	a
		font-family: $fontAlice
		font-size: 1.375rem
		text-decoration: none
		margin: 0 0.05rem
		padding: 0.1rem 0.75rem 0.25rem
		border: 2px solid rgba($mainColor, 0.25)
		border-radius: 1rem
		color: $mainColor

	a:visited
		color: $mainColor

	.activePage
		padding-left: 0.35rem
		
		&::a:visited
			color: darkred

		&::before
			display: inline-block
			font-family: $fontEmoji
			content: "\1F31F"
			font-size: 1.15rem
			margin-right: 0.25rem

	.subPage
		color: darkred !important
		
		&::after
			content: "\2026"
			color: darkred
			margin-left: 0

#subpage-nav
	display: none

main
	width: 82%
	min-height: 80vh
	margin-left: 4%
	
main>h1:first-child, hgroup h1
	font-size: 200%
	margin: 0.875rem auto
	text-align: center
	text-shadow: 0 0 1.5rem rgba($mainColor, 0.75)

#myhello
	width: 100%
	display: flex
	flex-flow: row nowrap

	.scarec
		align-self: start
		margin: 1rem 0.5rem 0 0

	.helloW
		flex: 3
		
		h1::before
			font-family: $fontEmoji
			content: "\1F44B"
			font-size: 1.5rem
			line-height: 0.8
			margin-right: 0.3rem

aside
	width: 13.4%
	box-sizing: border-box
	
	nav
		background-color: #ffff0025
		margin-top: 0.72rem
		padding: 0.75rem 0
		border: none
	
	nav a
		display: block
		box-sizing: border-box
		text-decoration: none
		text-align: center
		overflow: hidden
		text-overflow: ellipsis
		font-family: $fontAlice
		font-size: 1.15rem
		padding: 0.25rem 0.25rem 0.5rem 1.1rem
		margin-bottom: 0.75rem
		color: $mainColor !important

	nav a:last-child
		margin-bottom: 0
		
	.activeUrl
		border: 4px solid #00000050
		border-right: none
		border-top-left-radius: 1.5rem
		border-bottom-left-radius: 1.5rem
		background: url("../images/asign.svg") left center / 0.75rem 0.75rem no-repeat
		padding: calc(0.25rem - 4px) 0.25rem calc(0.5rem - 4px) calc(1.1rem - 4px)
		color: darkred !important
		
.firstpage
	margin: 1.5rem 0
	font-family: $fontContent
	
	p::first-letter
		font-weight: bolder
		color: maroon	

	p
		font-size: 1.5rem

footer
	width: 100%
	display: flex
	flex-flow: row nowrap
	justify-content: space-between
	font-size: 1.15rem
	background: $footerbg
	padding: 1.5rem 3rem
	margin-top: 0.5rem
	
	a
		text-decoration: none

.my-brand
	align-self: center
	font-weight: bolder
	border: none
	
	p
		line-height: 1.625rem
		font-family: $fontContent

.social
	font-family: $fontContent

.svg-icon
	width: 16px
	height: 16px
	display: inline-block
	fill: #828282
	padding-right: 0.35rem
	vertical-align: middle
	

	
