@import url('https://fonts.googleapis.com/css2?family=Alice&family=Marck+Script&family=Noto+Color+Emoji&family=Noto+Sans&family=Noto+Sans+Mono&family=Noto+Sans+Symbols&display=swap');

@font-face {
    font-family: 'aliceregular';
    src: url('../fonts/alice-regular-webfont.woff2') format('woff2'),
         url('../fonts/alice-regular-webfont.woff') format('woff'),
         url('../fonts/alice-regular-webfont.ttf') format('truetype'),
         url('../fonts/alice-regular-webfont.svg#aliceregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'marck_scriptregular';
    src: url('../fonts/marckscript-regular-webfont.woff2') format('woff2'),
         url('../fonts/marckscript-regular-webfont.woff') format('woff'),
         url('../fonts/marckscript-regular-webfont.ttf') format('truetype'),
         url('../fonts/marckscript-regular-webfont.svg#marck_scriptregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'noto_sansregular';
    src: url('../fonts/notosans-regular-webfont.woff2') format('woff2'),
         url('../fonts/notosans-regular-webfont.woff') format('woff'),
         url('../fonts/notosans-regular-webfont.ttf') format('truetype'),
         url('../fonts/notosans-regular-webfont.svg#noto_sansregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NotoColorEmoji';
    src: url('../fonts/NotoColorEmoji-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@import "mainstyle";
