$bodybg: url("../images/redline.svg") 86.5% top / 2px repeat-y, linear-gradient(to left, #f5f5f5, #ffffff00 5%, #ffffff00 95%, #f5f5f5), url("../images/kletka.svg") left top / 8rem 24rem repeat,  #fefefe;

$headerbg: url("../images/topchik.svg") center top / 100% 100% no-repeat, linear-gradient(to left, #f5f5f5, #ffffff00 5%, #ffffff00 95%, #f5f5f5), #fefefe;

$footerbg: url("../images/dungeon.svg") center bottom / 100% 100% no-repeat, linear-gradient(to left, #f5f5f5, #ffffff00 5%, #ffffff00 95%, #f5f5f5), #fefefe;